<h1 id="配置输出" tabindex="-1"><a class="header-anchor" href="#配置输出" aria-hidden="true">#</a> 配置输出</h1>
<p>在配置文件中，输出配置在<code>最外层</code>，使用<code>output</code>。输出配置是告诉webpack在哪里输出打包好的文件，并且告知其该如何生成打包文件。</p>
<h2 id="output-filename" tabindex="-1"><a class="header-anchor" href="#output-filename" aria-hidden="true">#</a> output.filename</h2>
